import React from 'react';
import Modal from 'react-modal';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import closebtn from '../images/closebtn.png';  //close button

import './PortfolioModal.css'

const customStyles = {
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)',
  },
};
 
// Make sure to bind modal to your appElement (http://reactcommunity.org/react-modal/accessibility/)
Modal.setAppElement('#root')

export default function App({ modalOpen, toggleModal, data }){
  const { 
    title, span, team, contents, stacks, 
    imgFiles, videoFiles,
    reference, githubRepo
  } = data
  return (
    <div>
      <Modal
        isOpen={modalOpen}
        onRequestClose={toggleModal}
        style={{...customStyles, 'overflow-y': 'scroll'}}
        contentLabel="Example Modal">

      <div className="modal-box">      
          <h1 id="main-title">{title}</h1>
          <Carousel infiniteLoop useKeyboardArrows
          showThumbs={false}>
          {imgFiles.map((img, index) => {
            return (
              <div key={index} className="carousel-container">
                <div className="portfolioModal-img" 
                  style={{
                    background: `url(${img.file})`, 
                    backgroundSize: 'contain', 
                    backgroundRepeat: 'no-repeat'
                  }}>
                </div>
                <p className="carousel-caption">{img.description}</p>
              </div>
            )
          })}

          {videoFiles ?
          videoFiles.map((video, index) => {
            return (
              <div key={index}>
                <video className="portfolioModal-img" controls >
                  <source src={video.file} type="video/mp4" />
                </video>
                <p className="carousel-caption">{video.description}</p>
              </div>
            )
          })
          : ''}
          </Carousel>
          
          <div className="portfolioModal-text">
            <h2 className="text-subtitle">About project</h2>
            <h4>{!team ? " 1인 프로젝트" : `Team : ${team}`}</h4>
            <h4>개발 기간 : {span}</h4>
            <p><span className="paragraph-space"></span>{contents.description}</p>
            <p><span className="paragraph-space"></span>{contents.description2}</p>
          </div>

          <div className="portfolioModal-text">
            <h2 className="text-subtitle">Stacks</h2>
            {stacks.map((stack, index) => { 
              return (
                  <ul className="set-list-padding" key={index}>
                    <li>{stack}</li>
                  </ul>
              )
            })}
          </div>

          <div className="portfolioModal-text">
            <h2 className="text-subtitle">Reference</h2>
            {reference.map((ref, index) => {
              return (
                <ul className="set-list-padding">
                  <li>{ref.title}
                  <a className="urlLink" href={ref.url} 
                  target="_blank" rel="noopener noreferrer">{ref.url}</a>
                  </li>
                </ul>
              )
            })}

            <h3>Github Repository</h3>
            <a className="urlLink" id="githubRepo" 
            href={githubRepo.client} 
            target="_blank" rel="noopener noreferrer">Client</a>
            <a className="urlLink" id="githubRepo"
            href={githubRepo.server} 
            target="_blank" rel="noopener noreferrer">Server</a>
            
            <img className="close-button" 
            src={closebtn} width='30px' height='30px' alt='' 
            onClick={toggleModal}/>
          </div>          
      </div>
      </Modal>
    </div>
  );
}
